/* Tablet and smaller screens */
@media (max-width: 768px) {
  /* Optimize section padding for smaller screens */
  .about-me-section {
    padding: 40px 20px;
    min-height: auto; /* Allow content to determine height */
  }

  /* Adjust main content card for better space usage */
  .about-me-content {
    padding: 35px;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
  }

  /* Improve intro section layout */
  .about-me-intro {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }

  /* Center text and adjust sizes */
  .about-me-intro-text {
    text-align: center;
    min-width: 100%;
  }

  .about-me-intro-text h2 {
    font-size: 2.3rem;
    margin-bottom: 1.2rem;
  }

  /* Optimize image size */
  .about-me-img {
    max-width: 240px;
    min-width: 200px;
    margin: 0 auto;
  }

  /* Improve skills layout */
  .skill-category ul {
    justify-content: center;
    gap: 10px;
  }

  .skill-category li {
    flex: 0 1 calc(50% - 15px); /* Two skills per row */
    justify-content: center;
    min-width: 130px;
  }

  /* Enhance social links presentation */
  .social-links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 12px;
    margin-top: 2rem;
  }

  .social-links a {
    margin: 0;
    min-width: 160px;
    justify-content: center;
  }
}

/* Phone-specific optimizations */
@media (max-width: 480px) {
  /* Further reduce padding for very small screens */
  .about-me-section {
    padding: 30px 15px;
  }

  .about-me-content {
    padding: 25px 20px;
  }

  /* Adjust text sizes for better readability */
  .about-me-intro-text h2 {
    font-size: 2rem;
  }

  .about-me-intro-text p {
    font-size: 0.95rem;
    line-height: 1.6;
  }

  /* Make skills full width on very small screens */
  .skill-category li {
    flex: 1 1 100%;
    width: 100%;
    max-width: none;
  }

  /* Stack social links on very small screens */
  .social-links {
    flex-direction: column;
    align-items: stretch;
  }

  .social-links a {
    width: 100%;
    padding: 10px 20px;
  }

  /* Adjust skill category headers */
  .skill-category h4 {
    font-size: 1.1rem;
    width: 100%;
    text-align: center;
  }
}